$main-color:  #FF4343;
$second-color: #2D3033;
$text-color: #6C7279;

@mixin btn {
    padding: 28px 0;
    background: $main-color;
    color: #FFF;
    text-align: center;
    font-size: 24px;
    line-height: 28px;
    font-weight: 900;
    text-transform: uppercase;
    display: block;

    &:hover {
        background: rgba(231, 20, 20, 1);
        transition: all 0.3s;
    }

    &:active {
        box-shadow: 4px 4px 30px 0px #FF4343;
        transition: all 0.3s;
    }

    @media (max-width: 767px) {
        padding: 18px 0;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: 'Raleway';
    font-style: normal;
    font-weight: 400;
}

a {
    text-decoration: none;
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;

    @media (max-width: 1024px) {
        max-width: 1014px;
        padding: 0 15px;
    }
}


.header {
    padding: 36px 0;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 100%;
    z-index: 2;

    @media (max-width: 767px) {
        padding: 21.4px 0;
    }

    &::after {
        content: "";
        width: 1170px;
        height: 1px;
        display: block;
        position: absolute;
        background: #BABBC5;
        left: 0;
        left: 50%;
        transform: translate(-50%, 0);
        top: 100%;

        @media (max-width: 1200px) {
            width: calc(100% - 40px);
        }

        @media (max-width: 767px) {
            display: none;
        }
    }

    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo-wrapper {
        max-width: 183px;
        display: inline-block;

        @media (max-width: 767px) {
            max-width: 103px;
        }
    }

    &__logo {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__burger-wrapper {
        max-width: 24px;
        display: none;

        @media (max-width: 890px) {
            display: block;
        }

        &__burger {
            width: 100%;
            height: 100%;
            object-fit: cover;

            @media (max-width: 890px) {
                fill: $main-color;
            }
        }
    }

    &__nav {
        @media (max-width: 890px) {
            display: none;
        }
    }

    .nav__list {
        display: flex;
        list-style: none;
        column-gap: 4px;

        .nav__item {
            min-width: 130px;
            text-align: center;
            // position: relative;
        }

        .nav__link {
            display: inline-block;
            font-weight: 700;
            font-size: 13px;
            line-height: 19px;
            text-align: center;
            text-transform: uppercase;
            color: #FFFFFF;
            width: 100%;
            position: relative;

            @media (max-width: 1024px) {
                font-size: 12px;
            }
        }

        .nav__link:active {
            color: #E71414;
            transition: all 0.3s;
        }

        .nav__link-active::after,
        .nav__link:hover::after {
            content: "";
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            border-bottom: 3px solid #BABBC5;
            position: absolute;
            transition: all 0.3s;
            transform: translateY(39px);
        }

        .nav__link-active:active:after,
        .nav__link:active::after {
            border-bottom: 3px solid #E71414;
        }
    }
}


.promo {
    padding: 230px 0 120px;
    background: url(../images/promo/promo-img.jpg) center/cover no-repeat;
    position: relative;
    margin-bottom: 116px;

    @media (max-width: 1024px) {
        padding: 257px 0 126px;
        margin-bottom: 78px;
    }

    @media (max-width: 767px) {
        padding: 106px 0 52px;
    }

    @media (max-width: 450px) {
        margin-bottom: 40px;
    }


    &::before {
        content: "";
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(37, 43, 73, 0.8);
        position: absolute;
    }

    &__wrapper {
        position: relative;
        z-index: 1;
    }

    &__title {
        color: #FFF;
        text-align: center;
        font-size: 64px;
        line-height: 75px;
        font-weight: 900;
        text-transform: uppercase;
        margin-bottom: 31px;

        @media (max-width: 1024px) {
            font-size: 48px;
            margin-bottom: 61px;
            line-height: 56px;
        }

        @media (max-width: 767px) {
            font-size: 36px;
            margin-bottom: 33px;
            line-height: 42px;
        }
    }

    &__text {
        color: #FFF;
        text-align: center;
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 79px;

        @media (max-width: 1024px) {
            font-size: 18px;
            margin-bottom: 89px;
            line-height: 21px;
        }

        @media (max-width: 767px) {
            font-size: 14px;
            margin-bottom: 51px;
            line-height: 16px;
        }
    }

    &__button {
        @include btn();
        max-width: 369px;
        margin: 0 auto;

        @media (max-width: 1024px) {
            max-width: 314px;
        }

        @media (max-width: 767px) {
            max-width: 100%;
        }
    }
}

.advantages {
    margin-bottom: 116px;

    @media (max-width: 1024px) {
        margin-bottom: 80px;
    }

    @media (max-width: 450px) {
        margin-bottom: 35px;
    }

    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        column-gap: 30px;
        row-gap: 88px;

        @media (max-width: 1024px) {
            column-gap: 20px;
            row-gap: 50px;
        }

        @media (max-width: 450px) {
            column-gap: 41px;
        }
    }

    &__item {
        width: calc((100% - 60px) / 3 );

        @media (max-width: 1024px) {
            width: calc((100% - 40px) / 3 );
        }

        @media (max-width: 630px) {
            width: calc((100% - 20px) / 2);
        }

        @media (max-width: 450px) {
            width: 100%;
        }
    }

    &__icon-wrapper {
        width: 100px;
        height: 100px;
        margin: 0 auto 54px;

        @media (max-width: 1024px) {
            width: 85px;
            height: 85px;
            margin-bottom: 45px;
        }

        @media (max-width: 450px) {
            width: 75px;
            height: 75px;
            margin-bottom: 38px;
        }
    }

    &__icon {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__title {
        margin-bottom: 44px;
        color: #2D3033;
        text-align: center;
        font-size: 24px;
        line-height: 28px;
        font-weight: 900;
        text-transform: uppercase;

        @media (max-width: 1024px) {
            margin-bottom: 24px;
            font-size: 18px;
            line-height: 21px;
        }

        @media (max-width: 450px) {
            margin-bottom: 9.5px;
        }
    }

    &__text {
        color: #6C7279;
        text-align: center;
        font-size: 18px;
        line-height: 21px;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
        }
    }
}

.work {
    margin-bottom: 114px;

    @media (max-width: 1024px) {
        margin-bottom: 78px;
    }

    @media (max-width: 450px) {
        margin-bottom: 35px;
    }

    &__container {
        display: flex;
        flex-wrap: wrap;
    }

    &__item-wrapper {
        width: calc(100% / 4);
        overflow: hidden;
        border: 1px solid #FFF;
        position: relative;
        aspect-ratio: 1;

        @media (max-width: 1024px) {
            aspect-ratio: 1/.89;
        }

        @media (max-width: 650px) {
            width: calc(100% / 2);
            aspect-ratio: 1/.9;
        }
    }

    &__item-image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__project-info {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        visibility: hidden;
        row-gap: 39px;
        cursor: pointer;

        @media (max-width: 1024px) {
            row-gap: 25px;
        }

        @media (max-width: 650px) {
            row-gap: 11px;
        }
    }

    &__project-name {
        color: #FFF;
        text-align: center;
        font-size: 24px;
        line-height: 28px;
        font-weight: 900;
        text-transform: uppercase;

        @media (max-width: 1024px) {
            font-size: 18px;
            line-height: 21px;
        }
    }

    &__project-text {
        color: #FFF;
        text-align: center;
        font-size: 18px;
        line-height: 21px;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
        }
    }

    &__item-wrapper:hover &__project-info {
        visibility: visible;
        cursor: pointer;
    }

    &__item-active &__project-info {
        visibility: visible;
        cursor: pointer;
    }

    &__item-wrapper:hover::before,
    &__item-active::before {
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        background: rgba(255, 67, 67, 0.80);
    }

    &__item-wrapper:active::before,
    &__item-active:active::before {
        background: rgba(185, 12, 12, 0.8);
    }
}

.team {
    margin-bottom: 114px;

    @media (max-width: 1024px) {
        margin-bottom: 78px;
    }

    @media (max-width: 450px) {
        margin-bottom: 34px;
    }

    &__wrapper {
        display: flex;
        column-gap: 30px;
        row-gap: 49px;
        flex-wrap: wrap;

        @media (max-width: 670px) {
            justify-content: space-between;
        }
    }

    &__item {
        flex: 1 0;

        @media (max-width: 670px) {
            flex: none;
            width: calc((100% - 49px)/2);
        }

        @media (max-width: 320px) {
            width: 100%;
        }
    }

    &__photo-wrapper {
        max-width: 270px;
        max-height: 270px;
        margin-bottom: 30px;
        overflow: hidden;

        @media (max-width: 1024px) {
            max-width: 227px;
            max-height: 227px;
            margin-bottom: 25px;
        }

        @media (max-width: 670px) {
            max-width: 295px;
            max-height: 295px;
        }

        @media (max-width: 320px) {
            max-height: 227px;
            display: flex;
            align-items: center;
            margin-bottom: 22px;
        }
    }

    &__photo {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__name {
        color: #2D3033;
        font-size: 24px;
        font-weight: 700;
        text-transform: capitalize;
        line-height: 28px;
        margin-bottom: 5px;

        @media (max-width: 1024px) {
            font-size: 18px;
            line-height: 21px;
        }
    }

    &__position {
        color: #FF4343;
        font-size: 18px;
        text-transform: uppercase;
        margin-bottom: 18px;
        line-height: 21px;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
            margin-bottom: 23px;
        }

        @media (max-width: 320px) {
            margin-bottom: 38px;
        }
    }

    &__text {
        color: #6C7279;
        font-size: 18px;
        line-height: 21px;
        margin-bottom: 38px;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
            margin-bottom: 39px;
        }

        @media (max-width: 320px) {
            margin-bottom: 55px;
        }
    }

    &__icons-wrapper {
        display: flex;
        column-gap: 5px;
    }

    &__icon-item {
        width: 39px;
        height: 39px;
        list-style: none;
        border: 1px solid #C6CBCD;

        @media (max-width: 1024px) {
            width: 33px;
            height: 33px;
        }

    }

    &__icon-link,
    &__icon {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__icon-link {
        display: inline-block;
        padding: 9px 9px;

        @media (max-width: 1024px) {
            padding: 7px 7px;
        }
    }


}

.footer {
    padding-top: 71px;
    background: #353C65;

    @media (max-width: 1024px) {
        padding-top: 46px;
    }

    @media (max-width: 535px) {
        padding-top: 34px;
    }

    &__wrapper {
        display: flex;
        column-gap: 30px;
        row-gap: 69px;
        margin-bottom: 67px;

        @media (max-width: 1024px) {
            column-gap: 20px;
            margin-bottom: 43px;
        }

        @media (max-width: 535px) {
            margin-bottom: 33px;
            flex-wrap: wrap;
        }
    }

    &__item {
        width: calc((100% - 60px)/3);

        @media (max-width: 1024px) {
            width: calc((100% - 40px)/3);
        }

        @media (max-width: 535px) {
            width: 100%;
        }
    }

    &__title {
        color: #FFF;
        text-align: center;
        font-size: 18px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 21px;
        margin-bottom: 29px;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
            margin-bottom: 17px;
        }

        @media (max-width: 535px) {
            margin-bottom: 34px;
        }
    }

    &__text {
        color: #FFF;
        text-align: center;
        font-size: 18px;
        text-transform: capitalize;
        line-height: 21px;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
        }
    }

    &__title-icons {
        margin-bottom: 48px;

        @media (max-width: 1024px) {
            margin-bottom: 32px;
        }

        @media (max-width: 535px) {
            margin-bottom: 37px;
        }
    }

    &__icons-wrapper {
        column-gap: 27px;
        justify-content: center;

        @media (max-width: 1024px) {
            column-gap: 23px;
        }

        @media (max-width: 535px) {
            column-gap: 17px;
        }
    }

    &__copirught-wrapper {
        background: #2C3154;
        padding: 24px 0;

        @media (max-width: 1024px) {
            padding: 14.4px 0;
        }

        @media (max-width: 535px) {
            padding: 29px 0;
        }
    }

    &__copiright {
        color: #808080;
        text-align: center;
        font-size: 18px;
        line-height: 21px;
        text-transform: capitalize;
        max-width: 370px;
        margin: 0 auto;

        @media (max-width: 1024px) {
            font-size: 14px;
            line-height: 16px;
            max-width: 320px;
        }

        @media (max-width: 535px) {
            padding: 29px 0;
        }
    }

    &__copiright-name {
        color: #FFF;
    }
}